}

.background {
  background: var(--color-bg);
  z-index: -1;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

:not(.nodebox--header) > .node-text {
  width: var(--column-width);
  margin-left: var(--column-left);
}

.background:after {
  content: "";
  background-image: linear-gradient(to right, transparent 0, var(--color-bg) var(--column-left), var(--color-bg) calc(var(--column-left)  + var(--column-width)), transparent 100%);
  opacity: .7;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

